import "./Registration.css";
import Form from "../../Form/Form";
import Button from "../../UI/Button/Button";
import InputField from "../../UI/InputField/InputField";
import ErrorMessage from "../../UI/ErrorMessage/ErrorMessage";

const Registration = ({
  auth,
  emailHandler,
  passwordHandler,
  login,
  password,
  name,
  setName,
  validatePassword,
  validateEmail,
  errors,
}) => {
  return (
    <Form
      name={""}
      onSubmit={auth}
      extraClass={"form__authorization "}
    >
      {name.length >= 128 ? (
        <ErrorMessage
          message={"    128 "}
        ></ErrorMessage>
      ) : null}

      <InputField
        value={name}
        onChange={(e) => setName(e.target.value)}
        placeholder={" "}
        type={"text"}
        width={350}
      />

      <InputField
        value={login}
        onChange={(e) => emailHandler(e.target.value)}
        placeholder={" email"}
        type={"email"}
        width={350}
      />
      <InputField
        value={password}
        onChange={(e) => passwordHandler(e.target.value)}
        placeholder={" "}
        type={"password"}
        minLength={8}
        width={350}
      />
      <div className='password__requirements'>
          :
        <br />-  8 ;
        <br /> -    ;
        <br /> -  ;
        <br /> - 
      </div>
      {errors !== null ? <ErrorMessage message={errors}></ErrorMessage> : null}

      <Button
        type='submit'
        text={""}
        disabled={
          name.toString().trim().length === 0 ||
          !validateEmail ||
          !validatePassword
        }
      />
    </Form>
  );
};

export default Registration;
